<template>
  <span>
    <span @click="emitTagClick">
      <a>
        <span>{{ tagname }}</span>
      </a>
    </span>
    <button @click="emitDeleteTag">
      x
    </button>
  </span>
</template>

<script>
export default {
  name: "tag-link",
  props:{
    tagname:{
      type: String,
      default: ''
    }
  },
  methods: {
    emitDeleteTag(){
      this.$emit('delete-tag', this.$refs.text)
    },
    emitTagClick(){
      this.$emit('tag-click', this.$refs.text)
    }
  }
}
</script>


